import React from 'react';
import AddClothesHeader from '../components/AddClothesHeader'
import Contact from "./Contact";
import Footer from "./Footer";
import FormStepOne from "../AddForm/FormStepOne";


const AddClothes = () => {
    return (
        <>
            <AddClothesHeader/>
            <FormStepOne/>
            <Contact/>
            <Footer/>
        </>
    );
}

export default AddClothes